<template>
  <div class="center-cmp">
    <div class="cc-header">
      <dv-decoration-1 style="width: 200px; height: 50px" />
      <div>学校分布</div>
      <dv-decoration-1 style="width: 200px; height: 50px" />
    </div>
    <div style="position: absolute; bottom: 20px; left: 20px; z-index: 2">
      <dv-decoration-7
        v-if="!start"
        style="width: 200px; height: 60px"
        @click="start = true"
        ><v-btn text class="white--text" @click="start = true"
          >开始轮播</v-btn
        ></dv-decoration-7
      >
      <template v-for="item in types">
        <dv-decoration-11
          :key="item.name"
          :color="item.type === currentType.type ? ['red', 'green'] : []"
          class="mb-4"
          style="width: 200px; height: 60px"
          ><v-btn text class="white--text" @click="changeCurrentType(item)">{{
            item.name
          }}</v-btn></dv-decoration-11
        ></template
      >
    </div>
    <div style="position: absolute; top: 100px; right: 40px">
      <dv-decoration-9 class="text-h5" style="width: 150px; height: 150px"
        >{{ currentSchoolCnt }}个</dv-decoration-9
      >
    </div>
    <div
      ref="shanximap"
      class="d-flex justify-center align-center"
      style="width: 100%; height: 100%; z-index: 1"
    ></div>
    <v-dialog v-model="flag" width="600px">
      <v-card color="transparent">
        <v-toolbar color="primary" class="text-h6" dark>{{
          currentItem.name
        }}</v-toolbar>
        <v-img
          src="https://poi-pic.cdn.bcebos.com/9aed42dcaec6891825dcc3aa28cca425.jpg"
        ></v-img>
        <v-card-text>
          <div class="text-body-2 text-start white--text">
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;武功县位于陕西省咸阳市西部，南隔渭河与周至相望，北与乾县接壤，西和杨陵区、扶风县毗邻，东与兴平市相连，县域面积397.8平方千米，辖普集街道办，游凤、苏坊、武功、贞元、长宁、大庄和小村7个镇，6个社区居委会，183个行政村，2021年末户籍总人口为43.41万人。
            武功县地处关中平原腹地，是中华民族农耕文明的发祥地。《国语·晋语》言：“昔少典氏娶于有蟜氏，生黄帝、炎帝。炎帝以姜水成，黄帝以姬水成。成而异德，故黄帝为姬，炎帝为姜。”姬水就是今天武功县境内的漆水河。后稷在今武功镇郑家坡建立中国历史上第一个邦国“有邰国”，成为周民族的先祖。周武王姬发灭殷商建西周，以公、侯、伯、子、男五等爵位分封亲属和功臣，让其建立诸侯国，并封山、封水、封神。感泰伯禅让之恩，随将秦岭主峰最高峰命名为“太白（泰伯）”山；纪念其文治武功，将太白山次峰（今称鳌山，古称垂山）命名为“武功山”，将斜水（今称石头河）命名为“武功水”。自周代以来，历朝历代将武功归属于长安都城的京畿之地。
            截至2021年末，武功县常住总人口为31.72万人。2021年，武功县实现地区生产总值171.35亿元，其中，第一产业完成27.5亿元，第二产业完成77.7亿元，第三产业完成66.2亿元。三次产业占地区生产总值的比重为16.1:45.3:38.6。人均地区生产总值53974元。
          </div>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn text outlined @click="flag = false" color="white">关闭</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
// import LabelTag from "./LabelTag";
import wugongMapJson from "@/assets/data/610431.json";
import schoolsJson from "@/assets/data/schools.json";
export default {
  name: "CenterCmp",
  components: {
    // LabelTag,
  },
  data() {
    return {
      config: {
        data: [
          {
            name: "收费站",
            value: 1315,
          },
          {
            name: "监控中心",
            value: 415,
          },
          {
            name: "道路外场",
            value: 90,
          },
          {
            name: "其他",
            value: 317,
          },
        ],
        color: ["#00baff", "#3de7c9", "#fff", "#ffc530", "#469f4b"],
        lineWidth: 30,
        radius: "55%",
        activeRadius: "60%",
      },

      labelConfig: {
        data: ["收费站", "监控中心", "道路外场", "其他"],
      },
      flag: false,
      types: [
        {
          name: "幼儿园",
          type: 1,
        },
        {
          name: "小学",
          type: 2,
        },
        {
          name: "初中",
          type: 3,
        },
        {
          name: "高中",
          type: 4,
        },
        {
          name: "职高",
          type: 5,
        },
      ],
      currentType: {
        name: "幼儿园",
        type: 1,
      },
      timer: null,
      animation: null,
      start: true,
      currentSchoolCnt: 0,
      currentItem: {},
    };
  },
  watch: {
    currentType: {
      // 每个属性值发生变化就会调用这个函数
      handler(newVal) {
        // console.log("oldVal:", oldVal, newVal);
        let datas = schoolsJson.filter((item) => item.type === newVal.type);
        this.currentSchoolCnt = datas.length;
        // this.$log.debug("newVal-currentType", datas);
        let option = {
          series: [
            {
              data: datas,
            },
            {
              data: datas
                .sort(function (a, b) {
                  return b.value[2] - a.value[2];
                })
                .slice(0, 5),
            },
          ],
        };
        if (this.shanXiMap) {
          this.shanXiMap.setOption(option, false);
        }
      },
      // 立即处理 进入页面就触发
      immediate: true,
      // 深度监听 属性的变化
      deep: true,
    },
    start: {
      // 每个属性值发生变化就会调用这个函数
      handler(newVal) {
        if (!newVal) {
          this.$log.debug("stop", newVal);
          this.animation.pause();
        } else {
          this.$log.debug("start", newVal);
          this.animation.play();
        }
      },
      // 立即处理 进入页面就触发
      immediate: false,
      // 深度监听 属性的变化
      deep: true,
    },
  },
  mounted() {
    this.generateWuGongMap();
    let that = this;
    let loopCompleted = 0;
    // this.timer = setInterval(function () {
    //   that.currentType = that.types[count++ % 5];
    //   that.$log.debug("test", that.currentType.type);
    // }, 2000);
    this.animation = this.$animejs({
      // targets: count,
      round: 1,
      easing: "linear",
      duration: 2000,
      loop: true,
      loopComplete: function () {
        // loopCompleted++;
        that.currentType = that.types[loopCompleted++ % 5];
        // that.$log.debug("test", );
      },
    });
  },
  methods: {
    changeCurrentType(item) {
      this.$log.debug("changeCurrentType", item);
      this.start = false;
      this.currentType = item;
    },
    async generateWuGongMap() {
      // this.$echarts.registerMap('shanxi', shanxiMapJson)
      this.$echarts.registerMap("610431", wugongMapJson);
      let datas = schoolsJson;
      this.currentSchoolCnt = datas.length;
      this.$log.debug("schools", datas);
      // await this.axios.post(this.$api.getMockDataHomeMap).then((response) => {
      //   datas = response.data.data;
      // });
      // this.$echarts.registerMap('shanxi', taiyuanMapJson)
      this.shanXiMap = this.$echarts.init(this.$refs.shanximap);

      // this.$log.debug('convertData', convertData(jsonData))

      const self = this;
      const option = {
        // backgroundColor: '#FFFFFF',
        title: {
          top: 0,
          x: "center",
          text: "",
          textStyle: {
            color: "#ccc",
          },
        },
        tooltip: {
          show: false,
          trigger: "item",
        },
        geo: {
          show: true,
          map: "610431",
          roam: true,
          zoom: 1.2,
          itemStyle: {
            show: false,
            normal: {
              borderColor: "#016cde",
              areaColor: "#2380f1",
              borderWidth: 2,
              shadowBlur: 30,
              shadowColor: "rgba(63, 218, 255, 0.5)",
            },
            emphasis: {
              areaColor: "#31d2ff",
            },
          },
          label: {
            normal: {
              show: false,
              formatter: function (params) {
                // self.$log.debug('params:', params)
                return params.name;
              },
            },
          },
          emphasis: {
            disabled: true,
          },
        },
        series: [
          {
            name: "credit_pm2.5",
            type: "scatter",
            coordinateSystem: "geo",
            data: datas,
            symbolSize: function (val) {
              // self.$log.debug('symbolSize', val)
              return val[2] / 10;
            },
            label: {
              show: true,
              formatter: "{xxxx|{b}}",
              position: "right",
              rich: {
                xxxx: {
                  color: "#cccccc",
                  textBorderColor: "#888888",
                  textBorderWidth: 2,
                },
              },
            },

            itemStyle: {
              normal: {
                color: "#05C3F9",
              },
            },
            tooltip: {
              position: [10, 10],
              formatter: "{b0}: {c0}<br />{b1}: {c1}",
            },
          },
          {
            name: "地图",
            type: "effectScatter",
            hoverAnimation: true,
            symbolSize: function (val) {
              // self.$log.debug('symbolSize2', val)
              return val[2] / 10;
            },
            label: {
              // normal: {
              //   show: true,
              //   position: 'right',
              //   formatter: '{b}'
              // }
              show: false,
              position: "bottom",
              distance: 0,
              formatter: function (params) {
                self.$log.debug("params:", params);
                return params.data.value[2];
              },
              textStyle: {
                color: "#fff",
                fontSize: 18,
                lineHeight: 30,
              },
            },
            itemStyle: {
              normal: {
                color: "#F4E925",
                shadowBlur: 10,
                shadowColor: "#05C3F9",
              },
            },
            coordinateSystem: "geo",
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
            },
            zlevel: 1,
            data: datas
              .sort(function (a, b) {
                return b.value[2] - a.value[2];
              })
              .slice(0, 5),
          },
        ],
      };
      this.shanXiMap.setOption(option, false);

      this.shanXiMap.on("click", (params) => {
        this.$log.debug("params", params);
        if (params.componentType === "series") {
          // this.$router.push({
          //   path: "/dashboard/home2",
          //   query: {
          //     target: params.data.name,
          //     adcode: params.data.adcode,
          //   },
          // });
          this.currentItem = params.data;
          this.flag = true;
          this.start = false;
        }
      });
      window.addEventListener("resize", () => {
        this.shanXiMap.resize();
      });
    },
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }, //生命周期 - 销毁之前
  unmounted() {
    window.addEventListener("resize", () => {});
  },
};
</script>

