户籍总人口为43.41万人。 武功县地处关中平原腹地，是中华民族农耕文明的发祥地。《国语·晋语》言：“昔少典氏娶于有蟜氏，生黄帝、炎帝。炎帝以姜水成，黄帝以姬水成。成而异德，故黄帝为姬，炎帝为姜。”姬水就是今天武功县境内的漆水河。后稷在今武功镇郑家坡建立中国历史上第一个邦国“有邰国”，成为周民族的先祖。周武王姬发灭殷商建西周，以公、侯、伯、子、男五等爵位分封亲属和功臣，让其建立诸侯国，并封山、封水、封神。感泰伯禅让之恩，随将秦岭主峰最高峰命名为“太白（泰伯）”山；纪念其文治武功，将太白山次峰（今称鳌山，古称垂山）命名为“武功山”，将斜水（今称石头河）命名为“武功水”。自周代以来，历朝历代将武功归属于长安都城的京畿之地。 截至2021年末，武功县常住总人口为31.72万人。2021年，武功县实现地区生产总值171.35亿元，其中，第一产业完成27.5亿元，第二产业完成77.7亿元，第三产业完成66.2亿元。三次产业占地区生产总值的比重为16.1:45.3:38.6。人均地区生产总值53974元。 ")])]),_c('v-card-actions',{staticClass:"justify-end"},[_c('v-btn',{attrs:{"text":"","outlined":"","color":"white"},on:{"click":function($event){_vm.flag = false}}},[_vm._v("关闭")])],1)],1)],1)],1)
